.pack {
    width: 320px;
    height: 480px;    
    border-radius: 10px;    
    background:linear-gradient(135deg, transparent 30px, #1698d9 0);    
    display: flex;
    align-items: center;
    justify-content: center;    
    margin-bottom: 14px;    
}

.pack__info {
    width: 312px;
    height: 472px;
    background:linear-gradient(135deg, transparent 28px, #f2f2f2 0);
    border-radius: 6px;
    overflow: hidden;
    position: relative;    
    padding: 20px 45px;
}

.pack__text {
    font-size: 16px;
}

.pack__text--focus {
    display: none;
    color: #e62e7a;
}

.pack__title {
    font-size: 48px;    
    line-height: 0.6;
    margin: 20px 0;    
}

.pack__title span {
    font-size: 24px;
}

.pack__present{
    list-style: none;  
    font-size: 14px; 
}

.pack__present,
.pack__text {
    color: #666;
}

.pack__img {
    position: absolute;
    left: -30px;
    bottom: -90px;
}

.pack__weight {
    width: 80px;
    height: 80px;
    border-radius: 40px;
    background: #1698d9;
    color: #fff;
    font-size: 42px;
    text-align: center;
    position: absolute;
    bottom: 10px;
    right: 10px;   
    display: flex;
    flex-direction: column;    
    justify-content: center;
    line-height: 0.9;
}

.pack__weight span {
    font-size: 21px;
}

.assortment__item-text,
.assortment__item-text--selected,
.assortment__item-text--desabled {
    color: #fff;
    font-size: 13px;
    line-height: 1.231;
    text-align: center;    
}

.assortment__item-buy {
    color: #2ea8e6;
    text-decoration: none;
    border-bottom: 1px dashed #1698d9;
    padding-bottom: 1px;   
}

.assortment__item-buy:hover {
    cursor: pointer;
}

.assortment__item-text--selected {    
    display: none;
}

.assortment__item-text--desabled {
    color: #ffff66;
    display: none;
    pointer-events: none;
}

.pack--default:hover .pack__weight,
.pack--default:focus .pack__weight {
    background: #2ea8e6;
}

.pack--default:hover,
.pack--default:focus {
    background:linear-gradient(135deg, transparent 30px, #2ea8e6 0);
    cursor: pointer;
    outline: none;
}

.pack--selected {
    background:linear-gradient(135deg, transparent 30px, #d91667 0);
    cursor: pointer;
    outline: none;
}

.pack--selected .pack__weight {
    background: #d91667;
}

.pack--selected ~ div .assortment__item-text--selected {
    display: block;
}

.pack--selected ~ div .assortment__item-text {
    display: none;
}

.pack--selected-hover:hover,
.pack--selected-hover:focus {
    background:linear-gradient(135deg, transparent 30px, #e62e7a 0);
    outline: none;
}

.pack--selected-hover:hover .pack__weight,
.pack--selected-hover:focus .pack__weight {
    background: #e62e7a;
}


.pack--selected-hover:hover .pack__text--focus,
.pack--selected-hover:focus .pack__text--focus {
    display: block;
}

.pack--selected-hover:hover .pack__text,
.pack--selected-hover:focus .pack__text {
    display: none;
}

.pack--disabled,
.pack--disabled:hover {
    background:linear-gradient(135deg, transparent 30px, #b3b3b3 0);   
    outline: none; 
}

.pack--disabled .pack__weight,
.pack--disabled:hover .pack__weight {
    background: #b3b3b3; 
}

.pack--disabled .pack__img {
    opacity: 0.6;
}

.pack--disabled .pack__info {
    background:linear-gradient(135deg, transparent 28px, #fff 0);    
    opacity: 0.8;
    color: #d4d4d4;
}

.pack--disabled .pack__text,
.pack--disabled .pack__present {    
    color: #d4d4d4;
}

.pack--disabled ~ div .assortment__item-text--desabled {
    display: block;
}

.pack--disabled ~ div .assortment__item-text,
.pack--disabled ~ div .assortment__item-text--selected {
    display: none;
}

.pack--disabled:hover {
    cursor: auto;
}
@font-face {
    font-family: "Exo 2.0 Thin";
    src: url(/funbox-test/static/media/Exo2.0-Thin.44bde874.otf) format("opentype");
}

body {    
    font-family: 'Trebuchet MS', sans-serif;
    padding: 0;
    margin: 0;
    background: url(/funbox-test/static/media/Background.cf966899.jpg) repeat;    
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

body:before {
    position: absolute;
    content:'';
    width: 100%;
    height: 100%;    
    top: 0;
    left: 0;    
    opacity: 0.5;    
    background: linear-gradient(to bottom, rgba(0,0,0,0.5) 10%, rgba(0,0,0,0) 30%, rgba(0,0,0,0) 70%, rgba(0,0,0,0.5) 100%);
    z-index: -1;
}  

* {
    box-sizing: border-box;
}

h1, h2, p, div, ul, li {
    margin: 0;
    padding: 0;
}

.container {    
    width: 1120px;
}

.assortment__title {
    font-size: 36px;
    line-height: 1.2;    
    color: #fff;
    text-align: center;
    font-weight: normal;
    margin: 0 0 20px 0;
    font-family: "Exo 2.0 Thin", sans-serif;
}

.assortment__list {
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 1200px) {  
    body {
        height: 100%;
    }  
    .container {    
        width: 720px;
    }

    .assortment__list {
        width: 100%;
        margin: 0 auto;
        flex-wrap: wrap-reverse;
        justify-content: center;
    }

    .assortment__list-item {
        margin: 0 20px 20px 20px;
    }
}

@media screen and (max-width: 720px) {
    body {
        height: 100%;
    }

    .container {    
        width: 100%;
    }

    .assortment__list-item {
        margin-bottom: 20px;        
    }

    .assortment__list {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
    }
}

@media screen and (max-width: 360px) {
    body {
        height: 100%;
    }
    .assortment__list-item {
        margin: 0 0 20px 0;
    }
}

@media screen and (min-height: 1120px) {
    body {
        height: 100vh;
    }
}
